import * as actionTypes from "../actions/actionTypes.js";

const INITIAL_STATE = {
  categoryList: null,
  isFetchingCategoryList: false,
  currentProductList: null,
  isFetchingProductList: false,
  currentCategory: null,
  toggleProductView:false,
};

const categoryReducer = (state = INITIAL_STATE, action) => {
  const { type, payLoad } = action;
  switch (type) {
    case actionTypes.GET_CATEGORY_LIST:
      return { ...state, categoryList: payLoad, isFetchingCategoryList: false };
    case actionTypes.SET_FETCHING_CATEGORY:
      return { ...state, isFetchingCategoryList: true };

    case actionTypes.GET_PRODUCT_LIST: {
      const {parent,productList,category} = payLoad;
      const currentCategory = [];
      currentCategory.push(category);
      parent && currentCategory.push(parent);
      return {
        ...state,
        isFetchingProductList: false,
        currentProductList:productList,
        currentCategory,
      };
    }
    case actionTypes.SET_FETCHING_PRODUCT_LIST:
      return { ...state, isFetchingProductList: true };
    case actionTypes.TOGGLE_PRODUCT_VIEW:
      return {...state,toggleProductView:!state.toggleProductView};
    case actionTypes.SET_PRODUCT_VIEW_FALSE:
      return {...state,toggleProductView:false}    
    default:
      return state;
  }
};

export default categoryReducer;
